.team {
  width: 100%;
  /* background-color: rgb(255, 244, 227); */
  padding: 2%;
}

.red-color {
  color: red;
}

.team h1 {
  text-align: center;
  margin: 1%;
}

.team-container {
  width: 100%;
  display: flex;
  padding: 2% 5% 1% 5%;
}

.team-box {
  width: 30%;
  height: 300px;
  background-color: white;
  margin: 1%;
  transition: 0.3s ease;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 0.1fr;
  box-shadow: 0px 0px 18px grey;
  border-radius: 18px;
  overflow: hidden;
}

.left {
  width: 100%;
  height: 100%;
  background-size: cover;
  overflow: hidden;
  transition: 0.3s ease;
}

.right {
  width: 100%;
  height: 100%;
  padding: 6%;
}

.team-box:hover {
  /* width: 33%; */
  box-shadow: 0px 0px 20px rgb(164, 164, 164);
}

.team-box:hover .left {
  scale: 1.1;
}

.small-text {
  font-size: small;
  font-weight: 50;
}

.chaitanya {
  background-image: url(../../assets/chaitanya.jpeg);
}

.harsha {
  background-image: url(../../assets/harsha.jpeg);
}

.abilash {
  background-image: url(../../assets/abilash.jpeg);
}

.chaithanya {
  background-image: url(../../assets/chaithanya.jpeg);
}

.rehaman {
  background-image: url(../../assets/rehaman.jpg);
}

@media only screen and (max-width: 912px) {
  .team-container {
    flex-wrap: wrap;
    /* flex-direction: column; */
  }
  .team-box {
    width: 39%;
    height: 300px;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 0.1fr;
    margin: 5% 5%;
  }
  /* .team-box:hover {
    width: 40%;
  } */
}

@media only screen and (max-width: 540px) {
  .team-container {
    flex-direction: column;
  }
  .team-box {
    width: 90%;
    height: 300px;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 0.1fr;
    margin: 5% 5%;
  }
  /* .team-box:hover {
    height: 350px;
    width: 90%;
  } */
  .team-box:hover .team-box-image {
    margin: 5% 0%;
    /* width: 50%; */
    height: 80%;
    border-radius: 0%;
  }
}
